import React from 'react';
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Typography from '@material-ui/core/Typography';
import Box from "@material-ui/core/Box";
import Link from "@material-ui/core/Link";

let Footers = [
    {
      title: "Compania",
      description: [
	  {label:"Quem somos", url:"#"},
	  {label:"História",url:"#"}, 
	  {label:"Fale conosco",url:"#"}]
    },
    {
      title: "Features",
      description: [
        {label:"Cool stuff", url:"#"},
        {label:"Random feature", url:"#"},
        {label:"Team feature", url:"#"},
        {label:"Developer stuff", url:"#"},
        {label:"Another one", url:"#"}
      ]
    },
    {
      title: "Resources",
      description: [
        {label:"Resource", url:"#"},
        {label:"Resource name", url:"#"},
        {label:"Another resource", url:"#"},
        {label:"Final resource", url:"#"}
      ]
    },
    {
      title: "Legal",
      description: [
	  {label:"Privacy policy", url:"#"}, 
	  {label:"Terms of use", url:"#"}
	  ]
    }
  ];

function MadeWithLove() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      Criado por <Link color="inherit" href="#">LINK AQUI</Link> equipe
    </Typography>
  );
}


/**
 * Footer (Rodapé)
 */
function Footer(classes) {
  return (
    <Container maxWidth="md" component="footer" className={classes.footer}>
      <Grid container spacing={4} justify="space-evenly">
        {Footers.map((footer, key) => (
          <Grid item xs={6} sm={3} key={key}>
            <Typography variant="h6" color="textPrimary" gutterBottom>
              {footer.title}
            </Typography>
            <ul>
              {footer.description.map(item => {
               
			   return (<div>{item.label}{item.url}</div>)
			   /*return (<li key={item}>
                  <Link href="#" variant="subtitle1" color="textSecondary">
                    {item}
                  </Link>
                </li>
              )*/
			  }
			  )}
            </ul>
          </Grid>
        ))}
      </Grid>
      <Box mt={5}>
        <MadeWithLove />
      </Box>
    </Container>
  );
}

export default Footer;